import { Carousel } from 'react-bootstrap';
import { useState } from 'react';
import { NavLink } from 'react-router-dom';

export default function Home() {
    const [index, setIndex] = useState(0)
    const handleSelect = (selectedIndex, e) => setIndex(selectedIndex)

    return (
        <>
        <Carousel activeIndex={index} onSelect={handleSelect} className="carousel">
            <Carousel.Item interval={5000} >
                <div className="carousel-item active" style={{backgroundImage: `url('${process.env.PUBLIC_URL + '/images/hero_01.jpg'}')`}}></div>
                {/* <img className="d-block w-100"  alt="First Slider" src="https://kavaglam.s3.ca-central-1.amazonaws.com/CFDD2143.JPG" /> */}
            </Carousel.Item>
            <Carousel.Item interval={5000} >
                <div className="carousel-item active" style={{backgroundImage: `url('${process.env.PUBLIC_URL + '/images/hero_02.jpg'}')`}}></div>
            </Carousel.Item>
            <Carousel.Item interval={5000} >
                <div className="carousel-item active" style={{backgroundImage: `url('${process.env.PUBLIC_URL + '/images/hero_03.jpg'}')`}}></div>
            </Carousel.Item>
            <Carousel.Item interval={5000} >
                <div className="carousel-item active" style={{backgroundImage: `url('${process.env.PUBLIC_URL + '/images/hero_04.jpg'}')`}}></div>
            </Carousel.Item>
            <Carousel.Item interval={5000} >
                <div className="carousel-item active" style={{backgroundImage: `url('${process.env.PUBLIC_URL + '/images/hero_05.jpg'}')`}}></div>
            </Carousel.Item>
        </Carousel>
        <div className='collection-list'>
            <h3>BRUSHIES BOUTIQUE'S</h3>
            <div>
                <div style={{backgroundImage: `url('${process.env.PUBLIC_URL + '/images/brush_01.jpg'}')`}}></div>
                <div style={{backgroundImage: `url('${process.env.PUBLIC_URL + '/images/brush_02.jpg'}')`}}></div>
                <div style={{backgroundImage: `url('${process.env.PUBLIC_URL + '/images/brush_03.jpg'}')`}}></div>
                <span>BRUSHES</span>
                <span>SPONGES</span>
                <span>LASHES</span>
            </div>
            <button><NavLink to='/products'>VIEW ALL PRODUCTS</NavLink></button>
        </div>
        </>
    )
}
