import { motion } from "framer-motion";
import { NavLink } from 'react-router-dom';
import { useSelector } from 'react-redux'
import { useState } from 'react';
import IconButton from '@material-ui/core/IconButton';
import Badge from '@material-ui/core/Badge';
import ShoppingCartOutlinedIcon from '@material-ui/icons/ShoppingCartOutlined';

export default function Navbar() {
    const { items, user } = useSelector(state=>({
        items: state.cart.addedItems,
        user: state.currentUser
    }))
    const [ isActive, setActive ] = useState(false)
    const toggleClass = (e) => { e.preventDefault(); setActive(!isActive) }

    return(
        <motion.nav initial={{ y: '-100vh' }} animate={{ y: 0 }} transition={{ delay: 1.5 }} className='topNavbar'>   
            <NavLink to='/'>
                <img src={process.env.PUBLIC_URL + '/images/kavaglam_logo_long.png'} alt="Kavaglam Logo" />
            </NavLink>
            <ul>
                <li>{user.admin ? (<NavLink to='/admin'>ADMIN</NavLink>) : (<NavLink to='/'>HOME</NavLink>) }</li>
                <li><NavLink to='/products'>BRUSHES</NavLink></li>
                <li><NavLink to='/' className='collapsible'>OTHER</NavLink></li>
                <li><NavLink to='/'>REVIEWS</NavLink></li>
                <li><NavLink to='/'>FAQ</NavLink></li>
                <li><NavLink to='/'>ABOUT</NavLink></li>
                <li>
                    <NavLink to='/cart'>
                        <IconButton aria-label="show cart" color="inherit">
                            <Badge badgeContent={items ? items.reduce((acc,i)=>{return acc+i.quantity}, 0) : 0 } color="secondary" />
                            <ShoppingCartOutlinedIcon />
                        </IconButton>
                    </NavLink>
                </li>
            </ul>
            <div className="hambuger" onClick={toggleClass}>
                <span></span>
                <span></span>
                <span></span>
            </div>
            <div className={isActive ? 'overlay' : 'hide'} >
                <div className={isActive ? 'nav-menu' : 'hide'}>
                    <a href='void(0)' onClick={ toggleClass }>
                        <i className='icon-close'>X</i>
                    </a>
                </div>
            </div>
        </motion.nav>
    )
}


